#videoBackground{
    height: 100vh;
    width: 100vw;
    position: relative;
}
#videoBackground video{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
#videoBackground .container{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(rgba(218, 236, 167, 0.8), transparent)
}
.home{
    position: fixed;
    left: 50%;
    bottom: 60px;
    color: #fff;
    transform: translateX(-50%);
    cursor: pointer;
  }