/*  */
   
   body {
       background-color: black;
   }
   
   #svg{
     background-color: darkseagreen;
     background: radial-gradient(darkseagreen 0%, black 70%);
     width: 100vw;
     height: 100vh;
   }

   #toMovePeople{
     position: fixed;
     left: 50%;
     bottom: 50px;
     color: #999;
     transform: translateX(-50%);
     cursor: pointer;
   }