#wrapper{
    background-color: rgba(0, 0, 0, 0.3);
    height: 100vh;
}
#wrapper .content{
    color: #fff;
    position: relative;
    height: 150px;
    /* margin-top: 200px; */
    top: 170px;
}
#wrapper .content h1{
    position: absolute;
    font-size: 36px;
    left: 50%;
    transform: translateX(-50%);
}
#wrapper .content p{
    top: 70px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

nav ul {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    margin-bottom: 0;
    list-style: none;
    padding-left: 0;
    border: solid 1px #ffffff;
    border-radius: 4px;
}
nav ul li {
    padding-left: 0;
    border-left: solid 1px #ffffff;
}
nav ul li:first-child {
    border-left: 0;
}
nav ul li a {
    display: block;
    min-width: 7.5rem;
    height: 2.75rem;
    line-height: 2.75rem;
    padding: 0 1.25rem 0 1.45rem;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
    font-size: 0.8rem;
    border-bottom: 0;
}
/* ul li a:hover {
    background-color: rgba(255, 255, 255, 0.075);
} */

@media screen and (max-width: 480px) {

 

   nav{
       width: 200px;
       margin: 0 auto;
       top: 250px;
       position: relative;
   }

         nav ul {
            -moz-flex-direction: column;
            -webkit-flex-direction: column;
            -ms-flex-direction: column;
            flex-direction: column;
            min-width: 10rem;
            max-width: 100%;
        }

             nav ul li {
                border-left: 0;
                border-top: solid 1px #ffffff;
            }

                 nav ul li:first-child {
                    border-top: 0;
                }

                 nav ul li a {
                    height: 3rem;
                    line-height: 3rem;
                    min-width: 0;
                    width: 100%;
                    text-decoration: none;
                    padding: 0;
                    text-align: center;
                }

         nav.use-middle:after {
            display: none;
        }

}