.switch{
    position: fixed;
    left: 50%;
    bottom: 70px;
    color: #fff;
    transform: translateX(-50%);
    cursor: pointer;
  }
  .private{
      bottom: 30px;
      color: #aaa;
  }

.snowfall-flakes{
    position:relative;
    width:5px;
    height:8px;
}
.snowfall-flakes:before,.snowfall-flakes:after{
    content:"";
    position:absolute;
    left:0px;
    top:0px;
    display:block;
    width:5px;
    height:8px;
    background:red;
    opacity: 0.5;
    border-radius:5px 5px 0 0;
    transform:rotate(-45deg);
}
.snowfall-flakes:after{
    left:4px;
    transform:rotate(45deg);
}
#love-rain{
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    position: absolute;
    overflow: hidden;
    background:url("../images/1.jpg") repeat-x center;/*添加背景图片*/
    background-size:100% 100%;/*背景尺寸：左右 上下*/
}
body ,html{
    background-color: #000;
}